import {actionTypes} from '@servicenow/ui-core';
import {
	componentNames, dataHandlerActions, NOW_LOGIN_SUCCEEDED,
	NOW_LOGIN_USER_ACTION_TAKEN, textLinkTypes, userActionNames, dataHandlerURLs
} from '../constants';
import {
	changePassword, changePasswordSucceeded, changePasswordFailed,
	fetchAuthStatus, fetchAuthStatusSucceeded, fetchAuthStatusFailed,
	loginUser, loginUserSucceeded, loginUserFailed,
	signInWithSSO, signInWithSSOSucceeded, signInWithSSOFailed
} from './dataHandlers';
import {actionNames as snlLocalLoginActionTypes} from '../../components/presentational/snl-local-login';
import {actionsNames as snlChangePasswordActionTypes} from '../../components/presentational/snl-change-pwd/index';
import {actionNames as snlSSOLoginActionTypes} from '../../components/presentational/snl-sso-login';
import {actionsNames as snlSocialLoginActionTypes} from '../../components/presentational/snl-social-login';
import {getAlertMessages, redirectUser} from '../utils';
import {getFirstFocusableElem} from '../../commons/utils';
import {SN_MFA_VALIDATE_SUCCEEDED,SN_MFA_VALIDATE_REDIRECT} from '@devsnc/sn-mfa-validate/src/sn-mfa-validate/constants';
import {actionNames as mfaSetupActions} from '@devsnc/sn-mfa-setup/src/sn-mfa-setup/constants';

function mfaSuccessCallback({action, updateState, dispatch}) {
	updateState({
		isLoading: false,
		messages: []
	});
	dispatch(NOW_LOGIN_SUCCEEDED, {url: action.payload.url});
}

export const actionHandlers = {
	[actionTypes.COMPONENT_CONNECTED]({state,dispatch}) {
		dispatch(dataHandlerActions.NL_FETCH_AUTH_STATUS,{[dataHandlerURLs.DEST_URL_PARAM]: state.properties.destinationUrl});
	},

	[actionTypes.COMPONENT_PROPERTY_CHANGED]({action, dispatch}) {
		const {name,value} = action.payload;
		if(name === 'destinationUrl' && value)
			dispatch(dataHandlerActions.NL_FETCH_AUTH_STATUS,{[dataHandlerURLs.DEST_URL_PARAM]: value});
	},

	[actionTypes.COMPONENT_RENDERED]({state, host}) {
		if(state.messages.length > 0) {
			const intervalId = setInterval(()=>{
				const alertElem = getFirstFocusableElem(host);
				if(alertElem && alertElem.getRootNode().host.tagName === 'NOW-ALERT') {
					alertElem.focus();
					clearInterval(intervalId);
				}
			}, 100);
		}
	},

	[dataHandlerActions.NL_FETCH_AUTH_STATUS]: fetchAuthStatus,
	[dataHandlerActions.NL_FETCH_AUTH_STATUS_SUCCEEDED]: fetchAuthStatusSucceeded,
	[dataHandlerActions.NL_FETCH_AUTH_STATUS_FAILED]: fetchAuthStatusFailed,

	[dataHandlerActions.NL_LOGIN_USER]: loginUser,
	[dataHandlerActions.NL_LOGIN_USER_SUCCEEDED]: loginUserSucceeded,
	[dataHandlerActions.NL_LOGIN_USER_FAILED]: loginUserFailed,

	[dataHandlerActions.NL_CHANGE_PWD]: changePassword,
	[dataHandlerActions.NL_CHANGE_PWD_SUCCEEDED]: changePasswordSucceeded,
	[dataHandlerActions.NL_CHANGE_PWD_FAILED]: changePasswordFailed,

	[dataHandlerActions.NL_SIGN_IN_WITH_SSO]: signInWithSSO,
	[dataHandlerActions.NL_SIGN_IN_WITH_SSO_SUCCEEDED]: signInWithSSOSucceeded,
	[dataHandlerActions.NL_SIGN_IN_WITH_SSO_FAILED]: signInWithSSOFailed,

	[dataHandlerActions.NL_REDIRECT_USER]({action}) {
		redirectUser(action.payload.redirect_url);
	},

	['NOW_ALERT#ACTION_CLICKED']: ({action, state, updateState}) => {
		updateState({
			messages: state.messages.filter(msgConfig => msgConfig.action.id !== action.payload.action.id)
		});
	},

	[snlLocalLoginActionTypes.LOGIN_CLICKED]({action, dispatch, updateState}) {
		const {userName, password, rememberMe} = action.payload;

		dispatch(NOW_LOGIN_USER_ACTION_TAKEN, {action: userActionNames.LOGIN});
		dispatch(dataHandlerActions.NL_LOGIN_USER, {
			data: {
				sysparm_type: 'api.login',
				sysparm_api: 'login',
				user_name: userName,
				user_password: password,
				remember_me: String(rememberMe)
			}
		});
		updateState({
			isLoading: true,
			messages: []
		});
	},

	[snlChangePasswordActionTypes.SUBMITTED]({action, dispatch, updateState}) {
		const {currentPwd, newPwd} = action.payload;

		dispatch(NOW_LOGIN_USER_ACTION_TAKEN, {action: userActionNames.CHANGE_PWD});
		dispatch(dataHandlerActions.NL_CHANGE_PWD, {
			data: {
				sysparm_type: 'api.login',
				sysparm_api: 'LOGIN_CPW',
				user_password_current: currentPwd,
				user_password: newPwd,
				user_password_validate: action.payload.confirmNewPwd,
			}
		});
		updateState({
			isLoading: true,
			messages: []
		});
	},

	[snlSSOLoginActionTypes.CONTINUE_CLICKED]({action, dispatch, updateState}) {
		const {userId} = action.payload;

		dispatch(NOW_LOGIN_USER_ACTION_TAKEN, {action: userActionNames.SSO});
		dispatch(dataHandlerActions.NL_SIGN_IN_WITH_SSO, {
			data: {
				sysparm_processor: 'MultiSSO_ClientHelper',
				sysparm_scope: 'global',
				sysparm_want_session_messages: 'true',
				sysparm_name: 'ssoByUser',
				sysparm_user_id: userId
			}
		});
		updateState({
			isLoading: true,
			messages: []
		});
	},

	[snlSocialLoginActionTypes.ITEM_CLICKED]({action, dispatch}) {
		dispatch(NOW_LOGIN_USER_ACTION_TAKEN, {action: userActionNames.SOCIAL});
		dispatch(dataHandlerActions.NL_REDIRECT_USER, {
			redirect_url: action.payload.action_url
		});
	},

	['NOW_BUTTON_BARE#CLICKED']: ({action, state, updateState}) => {
		const {target} = action.payload;

		let newState = {};
		switch(target) {
			case textLinkTypes.EXTERNAL_LOGIN:
				newState = {
					messages: [],
					compName: componentNames.SSO,
					compConfig: {},
					backConfig: {
						compName: state.compName,
						compConfig: state.compConfig
					}
				}; break;
			case textLinkTypes.USERNAME_PASSWORD:
				newState = {
					messages: [],
					compName: state.backConfig.compName,
					compConfig: state.backConfig.compConfig,
					backConfig: {}
				}; break;
		}
		updateState(newState);
	},

	[SN_MFA_VALIDATE_REDIRECT]({action, updateState}) {
		updateState({
			isLoading: false,
			compName: action.payload.redirect_to,
			compConfig: action.payload.META_DATA || {},
			messages: getAlertMessages(action.payload.messages)
		});
	},

	[SN_MFA_VALIDATE_SUCCEEDED]: mfaSuccessCallback,

	[mfaSetupActions.SETUP_SUCCEEDED]: mfaSuccessCallback,

	[mfaSetupActions.SETUP_BYPASSED]: mfaSuccessCallback,

	['NOW_TEXT_LINK#CLICKED']: ({action, dispatch}) => {
		if(action.payload.target === textLinkTypes.FORGOT_PWD)
			dispatch(NOW_LOGIN_USER_ACTION_TAKEN, {action: userActionNames.FORGOT_PWD});
	}
};

