import {translatedTexts as t} from '../../../commons/i18n';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-checkbox';

export const view = state => <div>
	<form>
		<now-input type='text' label={t.username} required={true} value={state.properties.userName} messages={state.userNameInfo.errMsg} invalid={state.userNameInfo.invalid} autofocus={!state.properties.userName}/>
		<now-input-password label={t.password} value="" required={true} messages={state.password.errMsg} invalid={state.password.invalid} autofocus={Boolean(state.properties.userName)}/>
		{state.properties.showRmrMe ? <now-checkbox label={t.rememberMe} checked={state.properties.isRmrMeEnabled}/> : null}
		<now-button label={t.login} variant="primary" size="md" />
	</form>
</div>;
