import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {actionNames} from './constants';
import {actionHandlers} from './actionHandlers';
import styles from './snl-sso-login.scss';
import {view} from './view';
import {handleFocus} from '../../../commons/utils';
import {translatedTexts as translated} from '../../../commons/i18n';


/**
* sso-login is the internal component for now-login
* allows login with sso
**/
createCustomElement('snl-sso-login', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
        * Specify heading for the component. e.g. _'Login  With SSO'_
        * @type String
        **/
		heading: {
			default: '',
			required: false,
			type: String
		},
		/**
        * pass userId as property if you want to login with particular userId
        * @type String
        **/
		userId:{
			default: '',
			required: false,
			type: String
		},
		/**
		* Specify label for input field  e.g. _'UserId'_
		**/
		label: {
			default: translated.userId,
			required: false,
			type: String
		}
	},
	initialState: {
		userIdInfo: {
			value: '',
			errMsg: [],
			invalid: false
		}
	},
	actionHandlers,
	dispatches: {
		/**
        * Dispached when user clicked continue button.
        * payload contains these properties
        * - `userId`: value of userName field.
        * @type object.{{userId: string}}
        **/
		[actionNames.CONTINUE_CLICKED]() {},

		/**
        * Dispached when user name is updated.
        * payload contains these properties
        * - `userId`: value of userId field.
        * @type {userId: string}
        **/
		[actionNames.USERID_CHANGED]() {}
	},
	eventHandlers: [{
		events: ['keydown'],
		effect({ host, action, dispatch }) {
			const {event} = action.payload;
			if (event.key === 'Enter') {
				event.preventDefault();
				handleFocus(host);
				dispatch(actionNames.ENTER_KEY_PRESSED);
			}
		}  
	}]
});
