import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import {view} from './view';
import {actionsNames} from './constants';

/**
 * ## Social Login Component
 *
 * @summary Renders social login component for logging via various social medias as configured by admin. The component just renders buttons based on array of items provided as property & dispatches action when any button is clicked.
 * @seismicElement snl-social-login
 */
createCustomElement('snl-social-login', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		 * Array of social media items that needs to be displayed as buttons. For each item object `label` property is required. While rendering 'Log in with' text will be prepended to the label.
		 * @type {Array.<{action_url: string, logo_url: string, name: string}>}
		 */
		items: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						action_url: {type: 'string'},
						logo_url: {type: 'string'},
						name: {type: 'string'}
					},
					required: ['name']
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on any button. Payload object is same as corresponding item object for which that button was rendered.
		 * - `action_url`: String. Value of clicked item's `url` property.
		 * - `logo_url`: String. Value of clicked item's `logo` property.
		 * - `name`: String. Value of clicked item's `label` property.
		 *
		 * @type {{url: string, logo: string, label: string}}
		 */
		[actionsNames.ITEM_CLICKED]: {
			schema: {
				type: 'object',
				properties: {
					action_url: {type: 'string'},
					logo_url: {type: 'string'},
					name: {type: 'string'}
				}
			}
		}
	}
});
