import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import {view} from './view';
import {changePwdActionHandlers} from './actionHandlers';
import {actionsNames} from './constants';
import {handleFocus} from '../../../commons/utils';

/**
 * ## Change Password Component
 *
 * @summary Renders change password component for changing password immediately after user logs in if configured by admin
 * @seismicElement snl-change-pwd
 */
createCustomElement('snl-change-pwd', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		currentPwd: {
			value: '',
			messages: [],
			invalid: false
		},
		newPwd: {
			value: '',
			messages: [],
			invalid: false
		},
		confirmNewPwd: {
			value: '',
			messages: [],
			invalid: false
		}
	},
	properties: {
		/**
		 * heading for the component. e.g. "Change Password"
		 * @type {String}
		 */
		heading: {},
		/**
		 * username for which password needed to be changed
		 * @type {String}
		 */
		username: {},
		/**
		 * Array of validations for the new password field. Each requirement
		 * must contain a label describing the requirement and the RegEx pattern
		 * against which the password must validate.
		 * @type {Array.<{label: string, pattern: RegExp}>}
		 */
		validations: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {label: {type: 'string'}, pattern: {type: 'string'}}
				}
			}
		},
		/**
		 * Array of inline suggestion messages that needs to be displayed below the new password field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: string, icon: string}>}
		 */
		messages: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						status: {
							type: 'string',
							enum: ['critical', 'warning', 'positive', 'info', 'suggestion']
						},
						content: {},
						icon: {type: 'string'}
					}
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on submit. Payload contains these properties:
		 * - `username`: String. Value of the `username` property.
		 * - `currentPwd`: String. Value entered in the current password field.
		 * - `newPwd`: String. Value entered in the new password field.
		 * - `confirmNewPwd`: String. Value entered in the confirm new password field.
		 *
		 * @type {{username: string, currentPwd: string, newPwd: string, confirmNewPwd: string}}
		 */
		[actionsNames.SUBMITTED]: {
			schema: {
				type: 'object',
				properties: {
					username: {type: 'string'},
					currentPwd: {type: 'string'},
					newPwd: {type: 'string'},
					confirmNewPwd: {type: 'string'}
				}
			}
		}
	},
	actionHandlers: changePwdActionHandlers,
	eventHandlers: [{
		events: ['keydown'],
		effect({ host, action, dispatch }) {
			const {event} = action.payload;
			if (event.key === 'Enter') {
				event.preventDefault();
				handleFocus(host);
				dispatch(actionsNames.ENTER_KEY_PRESSED);
			}
		}  
	}]
});
