import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import {view} from './view.js';
import {actionHandlers} from './actions/coreActions';
import {NOW_LOGIN_FAILED, NOW_LOGIN_SUCCEEDED, NOW_LOGIN_USER_ACTION_TAKEN} from './constants';

/**
 * ## Login Component
 *
 * @summary Renders login component for authenticating users
 * @seismicElement now-login
 */
createCustomElement('now-login', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	actionHandlers,
	initialState: {
		isLoading: true,
		messages: [],
		redirect_url: null,
		compName: null,
		compConfig: {},
		backConfig: {}
	},
	properties: {
		/**
		 * url of the company logo/brand
		 * @type {String}
		 */
		logoUrl: {
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * title/heading for login component, e.g. 'Welcome to ServiceNow'
		 * @type {String}
		 */
		heading: {
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * optional property to change Login with SSO label, e.g. 'External Login'
		 * @type {String}
		 */
		ssoLabel: {
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * optional property for deep-linking, e.g. '/now/ui/home'
		 * @type {String}
		 */
		destinationUrl: {
			required: false,
			schema: {
				type: 'string'
			}
		}
	},
	dispatches: {
		/**
		 * Dispatches if login is successful.The payload will contain redirect URL as value.
		 * @type {{url: string}}
		 */
		[NOW_LOGIN_SUCCEEDED]: {},

		/**
		 * Dispatches if login is failed
		 * @type {{message: string}}
		 */
		[NOW_LOGIN_FAILED]: {},

		/**
		 * Dispatches when user clicks any action button
		 * @type {{action: string}}
		 */
		[NOW_LOGIN_USER_ACTION_TAKEN]: {}
	}
});

