import {actionNames} from './constants';
import {actionTypes} from '@servicenow/ui-core';
import {getRequiredErrMsgs} from '../../../commons/utils';
import {translatedTexts as translated} from '../../../commons/i18n';

const handleValidations = ({state, dispatch, updateState}) => {
	const {value} = state.userNameInfo;
	const {password} = state;
	const payload = {
		userName: value,
		password: password.value,
		rememberMe: state.properties.isRmrMeEnabled
	};
	updateState({
		userNameInfo: {
			...state.userNameInfo,
			errMsg: value ? [] : [getRequiredErrMsgs(translated.userNameReqMsg)],
			invalid: !value
		},
		password: {
			...state.password,
			errMsg: password.value ? [] : [getRequiredErrMsgs(translated.pwdReqMsg)],
			invalid: !password.value,
		}
	});

	if(value && password.value)
		dispatch(actionNames.LOGIN_CLICKED, payload);
};

export const actionHandlers = {

	/**
    * Dispatch action 'SNL_LOCAL#LOGIN_CLICKED' when login button clicked, with payload-{username, password}
    **/
	'NOW_BUTTON#CLICKED': handleValidations,

	[actionTypes.COMPONENT_CONNECTED]({state, properties, updateState}){
		updateState({
			userNameInfo:{
				...state.userNameInfo,
				value: properties.userName
			}
		});
	},

	/**
    * Executes when enter key pressed,
    **/
	[actionNames.ENTER_KEY_PRESSED]: handleValidations,

	/**
    * Update error message when password is not valid.
    * Update password value in state.
    **/
	'NOW_INPUT_PASSWORD#INVALID_SET': ({action, updateState}) => {
		updateState({
			password: {
				value: action.payload.fieldValue,
				errMsg: action.payload.value ? [getRequiredErrMsgs(translated.pwdReqMsg)] : [],
				invalid: action.payload.value
			}
		});
	},

	/**
    * Update error message when userName is not valid.
    **/
	'NOW_INPUT#INVALID_SET': ({action, updateState}) => {
		updateState({
			userNameInfo: {
				value: action.payload.fieldValue,
				errMsg: action.payload.value ? [getRequiredErrMsgs(translated.userNameReqMsg)] : [],
				invalid: action.payload.value
			}
		});
	},
	/**
    * Update userName property and dispatch USERNAME_CHANGED action when userName is valid.
    **/
	'NOW_INPUT#VALUE_SET': ({action, updateProperties, dispatch}) => {
		updateProperties({
			userName: action.payload.value
		});
		dispatch(actionNames.USERNAME_CHANGED, {userName: action.payload.value});
	},

	['NOW_CHECKBOX#CHECKED_SET']: function({action, updateProperties}) {
		updateProperties({
			isRmrMeEnabled: action.payload.value
		});
	}

};
