/**
 * Returns status required to show alert message based on message status code
 * @param msgStatusCode
 * @returns {string}
 */
import {messageTypes} from './constants';

const getAlertStatusNIcon = msgType => {
	let status, icon;

	switch(msgType) {
		case messageTypes.ERROR: status = 'critical';  icon = 'circle-close-outline'; break;
		case messageTypes.INFO:
		default: status = 'info'; icon = 'bell-outline'; break;
	}

	return {
		status, icon
	};
};

/**
 * Returns a single alert message config object
 * @param message
 * @param msgType
 * @param id
 * @returns {{icon: string, action: {type: string}, id: string, content: *, status: string}}
 */
export const getAlertMsgConfig = (message, msgType, id) => {
	const alertConfig = getAlertStatusNIcon(msgType);
	return {
		status: alertConfig.status,
		icon: alertConfig.icon,
		content: message,
		action: {
			type: 'dismiss',
			id: id || `now-login-alert-${parseInt(Math.random()*100)}`,
		},
	};
};

/**
 * now-alert-list component uses this function to convert alert payload from backend into required format
 * @param messages
 */
export const getAlertMessages = messages => {
	if(!messages)
		return [];

	const newMessages = [];
	messages.forEach(msgConfig => {
		if(msgConfig.message)
			newMessages.push(getAlertMsgConfig(msgConfig.message, msgConfig.type));
	});
	return newMessages;
};


/**
 * AJAX calls require data in x-www-form-urlencoded form. This interceptor converts JSON data into raw format and adds
 * as form data in http request.
 * @param context
 * @example {name:'ssoByUser', userId: 'Abel.Tuter'} -> 'name=ssoByUser&userId=Abel.Tuter'
 */
export const convertDataInterceptor = context => {
	const {payload} = context.coeffects.action;
	const rawData = [];

	Object.keys(payload.data).forEach(parameter => {
		rawData.push(`${parameter}=${encodeURIComponent(payload.data[parameter])}`);
	});
	payload.data = rawData.join('&');

	return {
		...context
	};
};

/**
 * Redirect user to given URL
 * @param url
 */
export const redirectUser = url => {
	window.location.replace(url);
};

