import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import {translatedTexts as translated} from '../../../commons/i18n';


export const view = state => {
	const {heading, userId, label} = state.properties;
	return <div>
		<form>
			{heading ? <now-heading label={heading} level="1" variant="header-secondary"/> : ''}
			<now-input type='text'
				label={label}
				required={true}
				value={userId}
				messages={state.userIdInfo.errMsg}
				invalid={state.userIdInfo.invalid}
				autofocus={!userId}/>
			<slot name="captcha"/>
			<slot/>
			<now-button label={translated.continue} variant="primary" size="md" />
		</form>
	</div>;
};
