import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {actionNames} from './constants';
import {actionHandlers} from './actionHandlers';
import styles from './snl-local-login.scss';
import {view} from './view';
import {handleFocus} from '../../../commons/utils';

/**
* local-login is the internal component for now-login
* allows login with only username password.
**/
createCustomElement('snl-local-login', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		password: {
			value: '',
			invalid: false,
			errMsg: []
		},
		userNameInfo: {
			value: '',
			invalid: false,
			errMsg: []
		}
	},
	properties: {
		/**
        * Pass userName as property if you want to login with particular userName
        * @type String
        **/
		userName:{
			default: '',
			required: false,
			type: String
		},
		/**
		 * Status of remember me
		 */
		isRmrMeEnabled: {
			default: false,
			required: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Field used to show/hide Remember me checkbox
		 */
		showRmrMe: {
			default: false,
			required: false,
			schema: {
				type: 'boolean'
			}
		}
	},
	actionHandlers,
	eventHandlers: [{
		events: ['keydown'],
		effect({ host, action, dispatch }) {
			const {event} = action.payload;
			if (event.key === 'Enter') {
				event.preventDefault();
				handleFocus(host);
				dispatch(actionNames.ENTER_KEY_PRESSED);
			}
		}  
	}],
	dispatches: {
		/**
        * Dispached when user clicked login button.
        * payload contains these properties
        * - `userName`: value of userName field.
        * - `password`: value of password field.
        * @type object.{{userName: string, password: string}}
        **/
		[actionNames.LOGIN_CLICKED]() {},

		/**
        * Dispached when user name is updated.
        * payload contains these properties
        * - `userName`: value of userName field.
        * @type {userName: string}
        **/
		[actionNames.USERNAME_CHANGED]() {}

	}
});
