import {getFocusableNodes} from '@servicenow/behavior-focus';

const errorMsgConfig = ({
	type: 'critical',
	icon: 'circle-exclamation-outline'
});

export const getRequiredErrMsgs = msg => ({
	status: errorMsgConfig.type,
	content: msg,
	icon: errorMsgConfig.icon
});

export const handleFocus = host => {
	const {tabbableNodes} = getFocusableNodes(host);
	const {length} = tabbableNodes;
	if(!length)
		return ;
	tabbableNodes[length-1].focus();
};

export const getFirstFocusableElem = host => {
	if(!host.nodeType) 
		return;
	

	return getFocusableNodes(host).firstFocusableElement;
};

