import {actionsNames, fieldNames} from './constants';
import {translatedTexts as t} from '../../../commons/i18n';
import {getRequiredErrMsgs} from '../../../commons/utils';

function setInvalid(update,field,msg){
	update({
		path: field,
		value: {
			invalid: true,
			messages: [getRequiredErrMsgs(msg)]
		},
		operation: 'merge'
	});
}

/**
 *	Validates form inputs
  *	@param action
  * 	@param properties
  * 	@param state
  * 	@param dispatch
  * 	@param updateState
  */
function handleValidations({properties, state, dispatch, updateState}){
	let error = false;
	if(!state.currentPwd.value){
		setInvalid(updateState, 'currentPwd', t.currentPwdReqMsg);
		error = true;
	}
	if(!state.newPwd.value){
		setInvalid(updateState, 'newPwd', t.newPwdReqMsg);
		error = true;
	}
	if(!state.confirmNewPwd.value){
		setInvalid(updateState, 'confirmNewPwd', t.cnfNewPwdReqMsg);
		error = true;
	}else if(state.newPwd.value !== state.confirmNewPwd.value){
		setInvalid(updateState, 'confirmNewPwd', t.pwdMatchFailMsg); // set invalid but with a different error message
		error = true;
	}
	if(!error && !state.currentPwd.invalid && !state.newPwd.invalid && !state.confirmNewPwd.invalid){ // if no errors & fields are valid then only dispatch
		dispatch(actionsNames.SUBMITTED,{
			username: properties.username,
			currentPwd: state.currentPwd.value,
			newPwd: state.newPwd.value,
			confirmNewPwd: state.confirmNewPwd.value
		});
	}
}

export const changePwdActionHandlers = {
	/**
	 *	Executes when valid value is set for the field
	 *	@param action
	 * 	@param updateState
	 */
	['NOW_INPUT_PASSWORD#VALUE_SET']: ({action, updateState}) => {
		updateState({
			path: action.payload.name,
			value: {
				value: action.payload.value,
				invalid: false,
				messages: []
			},
			operation: 'assign'
		}); // set the field value and remove any error messages
	},
	/**
	 *	Executes everytime on blur of any password field
	 *	@param action
	 * 	@param updateState
	 */
	['NOW_INPUT_PASSWORD#INVALID_SET']: ({action, updateState}) => {
		let msg = '';
		switch (action.payload.name) {
			case fieldNames.NEW_PWD: msg = t.newPwdReqMsg;
				break;
			case fieldNames.CURRENT_PWD: msg = t.currentPwdReqMsg;
				break;
			case fieldNames.CNF_NEW_PWD: msg = t.cnfNewPwdReqMsg;
				break;
		}
		if(action.payload.name === 'confirmNewPwd' && action.payload.fieldValue)
			action.preventDefault(); // Incase password match error message, do nothing
		else{
			updateState({
				path: action.payload.name,
				value: {
					value: action.payload.fieldValue,
					invalid: action.payload.value,
					messages: action.payload.fieldValue ? [] : [getRequiredErrMsgs(msg)]
				},
				operation: 'assign'
			}); // set required error message in case value is empty
		}
	},
	/**
	 *	Executes when submit button is clicked
	 */
	['NOW_BUTTON#CLICKED']: handleValidations,
	/**
   *	Executes when enter key button is pressed.
	 */
	[actionsNames.ENTER_KEY_PRESSED]: handleValidations
};
