import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import {translatedTexts as t} from '../../../commons/i18n';

export const view = state => {
	const {properties,currentPwd,newPwd,confirmNewPwd} = state;
	const {heading,username,validations,messages} = properties;
	const newPwdMsgs = newPwd.messages.concat(messages); // For new password field, concat component level validation messages with messages provided as property
	return (
		<div>
			<form>
				{heading ? <now-heading label={heading} level="1" variant="header-secondary" /> : ''}
				<now-input label={t.username} value={username} readonly />
				<now-input-password name="currentPwd" label={t.currentPassword} invalid={currentPwd.invalid} messages={currentPwd.messages} required disableUnmask autofocus />
				<now-input-password name="newPwd" label={t.newPassword} invalid={newPwd.invalid} messages={newPwdMsgs} requirements={validations} required disableUnmask />
				<now-input-password name="confirmNewPwd" label={t.confirmNewPassword} invalid={confirmNewPwd.invalid} messages={confirmNewPwd.messages} required disableUnmask />
				<now-button label={t.submit} variant="primary" size="md" />
			</form>
		</div>
	);
};
