import '@servicenow/now-button';
import {btnStaticConfig, actionsNames, MAX_ITEMS_IN_LIST} from './constants';
import {translatedTexts as translated} from '../../../commons/i18n';

function btnConfig(dispatch,item){
	return {
		...btnStaticConfig,
		configAria: {'aria-label': translated.logInWith(item.name)},
		'on-click': () => {
			dispatch(() => ({
				type: actionsNames.ITEM_CLICKED,
				payload: item
			}));
		}
	};
}

function renderListView(items,dispatch){
	return (
		<div className="list">
			{items.map(item=>(
				<now-button {...btnConfig(dispatch,item)}>
					{item.logo_url ? <img alt={translated.icon} src={item.logo_url} /> : null}
					<p className={'overflow-ellipsis'}>{translated.logInWith(item.name)}</p>
				</now-button>
			))}
		</div>
	);
}

function renderTileView(items,dispatch){
	return (
		<div className="tile">
			{items.map(item=>(
				<now-button tooltipContent={translated.logInWith(item.name)} {...btnConfig(dispatch,item)}>
					{item.logo_url ? <img alt={translated.icon} src={item.logo_url} /> : item.name.charAt(0).toUpperCase()}
				</now-button>
			))}
		</div>
	);
}

export const view = (state,{dispatch}) => {
	const allItems = state.properties.items;
	if(!allItems.length)
		return;
	else if(allItems.length<=MAX_ITEMS_IN_LIST)
		return renderListView(allItems,dispatch);

	return renderTileView(allItems,dispatch);

};
