import {createHttpEffect} from '@servicenow/ui-effect-http';
import {
	dataHandlerActions,
	NOW_LOGIN_SUCCEEDED, NOW_LOGIN_FAILED, dataHandlerURLs, messageTypes, componentNames
} from '../constants';
import {convertDataInterceptor, getAlertMsgConfig, getAlertMessages} from '../utils';


export const fetchAuthStatus = createHttpEffect(dataHandlerURLs.AUTH_STATUS, {
	method: 'GET',
	batch: false,
	queryParams: [dataHandlerURLs.DEST_URL_PARAM],
	successActionType: dataHandlerActions.NL_FETCH_AUTH_STATUS_SUCCEEDED,
	errorActionType: dataHandlerActions.NL_FETCH_AUTH_STATUS_FAILED
});

export const fetchAuthStatusSucceeded = ({action, dispatch, updateState}) => {
	const {redirect_to} = action.payload;
	if(action.payload.code === 200) {
		updateState({
			isLoading: false,
			messages: [],
			compName: componentNames.LOGIN
		});
		return;
	}else if(action.payload.code === 702){
		dispatch(dataHandlerActions.NL_REDIRECT_USER, {
			redirect_url: redirect_to
		});
		return;
	}

	updateState({
		isLoading: false,
		messages: getAlertMessages(action.payload.messages),
		compName: redirect_to,
		compConfig: action.payload.META_DATA || {}
	});
};

export const fetchAuthStatusFailed = ({action, dispatch, updateState}) => {
	updateState({
		messages: [getAlertMsgConfig(action.payload.message, messageTypes.ERROR, 'now-login-auth-status-1')],
		compConfig: null,
		isLoading: false
	});

	dispatch(NOW_LOGIN_FAILED);
};

export const loginUser = {
	...createHttpEffect(dataHandlerURLs.DEFAULT_ROUTE, {
		method: 'POST',
		batch: false,
		dataParam: 'data',
		headers: {
			'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
		},

		successActionType: dataHandlerActions.NL_LOGIN_USER_SUCCEEDED,
		errorActionType: dataHandlerActions.NL_LOGIN_USER_FAILED
	}),
	interceptors: [convertDataInterceptor]
};

export const loginUserSucceeded = ({action, dispatch, state, updateState}) => {
	const {code, redirect_to} = action.payload;
	if(code === 200) {
		updateState({
			isLoading: false,
			messages: []
		});
		dispatch(NOW_LOGIN_SUCCEEDED, {url: redirect_to});
		return;
	} else if (code !== 307)
		dispatch(NOW_LOGIN_FAILED, { message: action.payload.message });

	updateState({
		isLoading: false,
		messages: getAlertMessages(action.payload.messages),
		compName: redirect_to ? redirect_to : state.compName,
		compConfig: redirect_to ? action.payload.META_DATA : state.compConfig,
		backConfig: {
			compName: state.compName,
			compConfig: state.compConfig
		}
	});
};

export const loginUserFailed = ({action, dispatch, updateState}) => {
	updateState({
		isLoading: false,
		messages: [getAlertMsgConfig(action.payload.message, messageTypes.ERROR, 'now-login-1')],
	});
	dispatch(NOW_LOGIN_FAILED, { message: action.payload.message });
};

export const changePassword = {
	...createHttpEffect(dataHandlerURLs.DEFAULT_ROUTE, {
		method: 'POST',
		batch: false,
		dataParam: 'data',
		headers: {
			'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
		},

		successActionType: dataHandlerActions.NL_CHANGE_PWD_SUCCEEDED,
		errorActionType: dataHandlerActions.NL_CHANGE_PWD_FAILED
	}),
	interceptors: [convertDataInterceptor]
};

export const changePasswordSucceeded = ({action, dispatch, updateState}) => {
	if(action.payload.code === 200) {
		updateState({
			isLoading: false,
			messages: []
		});
		dispatch(NOW_LOGIN_SUCCEEDED, {url: action.payload.redirect_to});
		return;
	}

	updateState({
		isLoading: false,
		compName: action.payload.redirect_to,
		compConfig: {
			user_name: action.payload.user_name
		},
		messages: getAlertMessages(action.payload.messages)
	});
};

export const changePasswordFailed = ({action, updateState}) => {
	updateState({
		isLoading: false,
		messages: [getAlertMsgConfig(action.payload.message, messageTypes.ERROR, 'now-login-change-pswd-1')],
	});
};

export const signInWithSSO = {
	...createHttpEffect(dataHandlerURLs.SSO_LOGIN, {
		method: 'POST',
		batch: false,
		dataParam: 'data',

		successActionType: dataHandlerActions.NL_SIGN_IN_WITH_SSO_SUCCEEDED,
		errorActionType: dataHandlerActions.NL_SIGN_IN_WITH_SSO_FAILED
	}),
	interceptors: [convertDataInterceptor]
};

export const signInWithSSOSucceeded = ({action, dispatch, updateState}) => {
	const parser = new DOMParser();
	const responseXML = parser.parseFromString(action.payload, 'application/xml');
	const results = responseXML.getElementsByTagName('result');

	if(results && results.length > 0) {
		const sso_id = results[0].getAttribute('glide_sso_id');
		if(sso_id) {
			dispatch(dataHandlerActions.NL_REDIRECT_USER, {
				redirect_url: `login_with_sso.do?glide_sso_id=${sso_id}`
			});
			return;
		}

		const discovery_service_url = results[0].getAttribute('discovery_service_url');
		if(discovery_service_url) {
			dispatch(dataHandlerActions.NL_REDIRECT_USER, {
				redirect_url: discovery_service_url
			});
		}
	} else {
		const errorMessage = responseXML.documentElement.getAttribute('error');
		updateState({
			isLoading: false,
			messages: [getAlertMsgConfig(errorMessage, messageTypes.ERROR, 'now-login-sso-1')]
		});
	}
};

export const signInWithSSOFailed = ({action, updateState}) => {
	updateState({
		isLoading: false,
		messages: [getAlertMsgConfig(action.payload.message, messageTypes.ERROR, 'now-login-sso-1')],
	});
};

