import {actionNames} from './constants';
import {actionTypes} from '@servicenow/ui-core';
import {getFocusableNodes, handleFocusFirstNode} from '@servicenow/behavior-focus';
import {getRequiredErrMsgs} from '../../../commons/utils';
import {translatedTexts as translated} from '../../../commons/i18n';

let requiredMsg = '';

const handleValidations = ({state, dispatch, updateState}) => {
	const {value} = state.userIdInfo;
	updateState({
		userIdInfo: {
			...state.userIdInfo,
			errMsg: value ? [] : [getRequiredErrMsgs(requiredMsg)],
			invalid: !value
		}
	});

	if(value)
		dispatch(actionNames.CONTINUE_CLICKED, {userId: value});
};

//Set focus when component loads for the first time
const setInitialFocus = ({host}) => {
	const {tabbableNodes} = getFocusableNodes(host);
	//set focus on second focusable/tabbable element
	if(tabbableNodes.length > 1)
		tabbableNodes[1].focus();
};

export const actionHandlers = {

	[actionNames.SET_FOCUS]: setInitialFocus,

	[actionTypes.COMPONENT_CONNECTED]({properties, state, host, updateState, dispatch}) {
		updateState({
			userIdInfo: {
				...state.userIdInfo,
				value:properties.userId,
			}
		});
		requiredMsg = translated.userIdReqMsg(properties.label);
		if(!properties.userId)
			return;
		handleFocusFirstNode(host, dispatch(actionNames.SET_FOCUS));
	},

	/**
    * Dispatch action 'CONTINUE_CLICKED' when continue button clicked, with payload-{userId: userId}
    **/
	'NOW_BUTTON#CLICKED': handleValidations,

	/**
    * Executes when enter key pressed,
    **/
	[actionNames.ENTER_KEY_PRESSED]: handleValidations,

	/**
    * Update error message when userId is not valid.
	* Update userId in properties and dispatch USERID_CHANGED action.
    **/
	'NOW_INPUT#INVALID_SET': ({action, updateState}) => {
		updateState({
			userIdInfo: {
				value: action.payload.fieldValue,
				errMsg: action.payload.value ? [getRequiredErrMsgs(requiredMsg)] : [],
				invalid: action.payload.value
			}
		});
	},
	/**
    * Update userId property and dispatch USERID_CHANGED action when userName is valid.
    **/
	'NOW_INPUT#VALUE_SET': ({action, updateProperties, dispatch}) => {
		updateProperties({
			userId: action.payload.value
		});
		dispatch(actionNames.USERID_CHANGED, {userId: action.payload.value});
	}

};
