import '@servicenow/now-heading';
import '@servicenow/now-loader';
import '@servicenow/now-alert';
import '@servicenow/now-checkbox';
import '@devsnc/sn-mfa-setup';
import '@devsnc/sn-mfa-validate';
import '../components/presentational/snl-local-login';
import '../components/presentational/snl-social-login';
import '../components/presentational/snl-change-pwd';
import '../components/presentational/snl-sso-login';
import {textLinkTypes, componentNames} from './constants';
import {translatedTexts as t} from '../commons/i18n';
import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';

export const view = state => {
	if(state.isLoading && !state.compName)
		return renderLoader(true);

	const containerClass = `login-container${  state.isLoading ? ' overlay' : ''}`;
	return (
		<div className={'login-wrapper'}>
			{renderLoader(state.isLoading, 'loader')}
			<div className={containerClass}>
				{renderAlert(state.messages)}
				{ state.properties.logoUrl ? <div className="login-logo">
					<img src={state.properties.logoUrl} alt={t.logoAltText} />
				</div> : null}
				{renderComponent(state)}
			</div>
		</div>
	);
};

const renderComponent = state => {
	const {compName} = state;
	if(!compName)
		return;

	switch(compName) {
		case componentNames.CHANGE_PWD: return renderChangePwd(state.compConfig);
		case componentNames.SSO: return renderSSOLogin(state.compConfig);
		case componentNames.MFA_SETUP: return renderMFASetup(state.compConfig);
		case componentNames.MFA_VALIDATE: return renderMFAValidate(state.compConfig);
		case componentNames.LOGIN:
		default: return renderLocalLogin(state.properties, state.compConfig);
	}
};

const renderMFASetup = config => <sn-mfa-setup config={config} heading={t.mfaSetupHeading} isUserRequested={false}/>;

const renderMFAValidate = config => <sn-mfa-validate config={config} heading={t.mfaValidateHeading} actionBtnLabel={t.login}/>;

const renderLocalLogin = (props, compConfig = {}) => (
	<Fragment>
		{ props.heading ? <now-heading className={'heading'} label={props.heading} level="1" variant="header-secondary"/> : null}
		<snl-local-login userName={compConfig.userName} showRmrMe={compConfig.remember_me} isRmrMeEnabled={compConfig.remember_me_default}/>

		<div className={'login-meta'}>
			{compConfig.external_login
				? <now-button-bare label={props.ssoLabel || t.loginWithSSO} variant="secondary" size="sm" append-to-payload={{target: textLinkTypes.EXTERNAL_LOGIN}}/> : null}
			{compConfig.forgot_password
				? <now-text-link label={t.forgotPassword} href={compConfig.forgot_password} variant="primary" opensWindow={true} append-to-payload={{target: textLinkTypes.FORGOT_PWD}}/> : null}
		</div>

		{
			compConfig.idp_records && compConfig.idp_records.length > 0
				? <p className={'component-divider'}><span className="line-center">{t.orSignInWith}</span></p>
				: null
		}

		{renderSocialLogin(compConfig.idp_records)}
	</Fragment>
);

const renderSocialLogin = itemsArr => {
	if(!itemsArr || !itemsArr.length)
		return;

	return <snl-social-login items={itemsArr}/>;
};

const renderChangePwd = (compConfig = {}) => (
	<snl-change-pwd heading={compConfig.heading || t.changePassword} username={compConfig.user_name}
		messages={compConfig.messages} validations={compConfig.validations}/>
);

const renderSSOLogin = (compConfig = {}) => (
	<Fragment>
		<snl-sso-login heading={compConfig.heading || t.signInWithSSO} userId={compConfig.userName} />
		<now-button-bare label={t.loginWithUsernameNPassword} variant="secondary" size="sm" append-to-payload={{target: textLinkTypes.USERNAME_PASSWORD}}/>
	</Fragment>
);

const renderAlert = messages => {
	if(!messages || !messages.length)
		return;

	return messages.map(msgConfig => <now-alert status={msgConfig.status} icon={msgConfig.icon}
		content={msgConfig.content} action={msgConfig.action}/>);
};

const renderLoader = (showLoader, className) => showLoader ? <now-loader label={`${t.loading}`} size="lg" className={className}/> : null;
